import { gapi } from 'gapi-script'
import { useEffect, useState } from 'react'

import { CalendarEvent } from '@/types/calendar'
import {
  getAuthInstance,
  initGapiClient,
  loadEvents,
} from '@utils/calendarHelpers'

export const useCalendar = () => {
  const [events, setEvents] = useState<CalendarEvent[]>([])
  const [isSignedIn, setIsSignedIn] = useState<boolean>(() => {
    return localStorage.getItem('isSignedIn') === 'true'
  })
  const [error, setError] = useState<string>('')
  const [loading, setLoading] = useState<boolean>(false)

  useEffect(() => {
    const initClient = async () => {
      await initGapiClient()
      const authInstance = getAuthInstance()
      const signedIn = authInstance.isSignedIn.get()
      setIsSignedIn(signedIn)
      localStorage.setItem('isSignedIn', signedIn.toString())

      if (signedIn) {
        await loadEvents(setEvents, setError, setLoading)
      }

      authInstance.isSignedIn.listen(async (isSignedIn: boolean) => {
        setIsSignedIn(isSignedIn)
        localStorage.setItem('isSignedIn', isSignedIn.toString())
        if (isSignedIn) {
          await loadEvents(setEvents, setError, setLoading)
        } else {
          setEvents([])
        }
      })
    }

    gapi.load('client:auth2', initClient)
  }, [])

  useEffect(() => {
    if (isSignedIn) {
      loadEvents(setEvents, setError, setLoading)
    } else {
      setEvents([])
    }
  }, [isSignedIn])

  const signIn = async () => {
    const authInstance = getAuthInstance()
    await authInstance.signIn()
  }

  const signOut = () => {
    getAuthInstance().signOut()
  }

  return { events, isSignedIn, error, loading, signIn, signOut }
}
