import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { fetchWeatherError } from '@/store/actions/weatherActions'
import { AppDispatch } from '@/store/types'
import { fetchWeatherData } from '@/store/workers/weatherWorkers'
import { WeatherHookProps } from '@/types/weather'
import { RootState } from '@store/rootReducer'

export const useWeather = ({
  selectedCity: city,
  latitude,
  longitude,
}: WeatherHookProps) => {
  const dispatch = useDispatch<AppDispatch>()

  const currentWeather = useSelector(
    (state: RootState) => state.weather.currentWeather
  )
  const forecastWeather = useSelector(
    (state: RootState) => state.weather.forecastWeather
  )
  const hourlyWeather = useSelector(
    (state: RootState) => state.weather.hourlyWeather
  )
  const loading = useSelector((state: RootState) => state.weather.loading)
  const error = useSelector((state: RootState) => state.weather.error)

  useEffect(() => {
    if (!city && (!latitude || !longitude)) {
      dispatch(fetchWeatherError('City or coordinates are required'))
    } else {
      dispatch(fetchWeatherData(city, latitude, longitude))
    }
  }, [city, latitude, longitude, dispatch])

  return { currentWeather, forecastWeather, hourlyWeather, loading, error }
}
