import styled from 'styled-components'

export const Container = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    flex-direction: column;

    margin-bottom: ${({ theme }) => theme.margin.large};
  }
`
export const WeatherIcon = styled.img`
  width: ${({ theme }) => theme.iconSize.large};
  height: ${({ theme }) => theme.iconSize.large};

  @media (max-width: ${({ theme }) => theme.media.largeDesktop}) {
    width: ${({ theme }) => theme.iconSize.medium};
    height: ${({ theme }) => theme.iconSize.medium};
  }
  @media (max-width: ${({ theme }) => theme.media.desktop}) {
    width: ${({ theme }) => theme.iconSize.small};
    height: ${({ theme }) => theme.iconSize.small};
  }
`
export const TextContainer = styled.div`
  margin: 0 5%;

  display: flex;
  flex-direction: column;
  align-items: center;

  text-align: center;
`
export const Day = styled.h3`
  font-size: ${({ theme }) => theme.fonts.fontSizes.large};
  text-align: center;

  @media (max-width: ${({ theme }) => theme.media.largeDesktop}) {
    font-size: ${({ theme }) => theme.fonts.fontSizes.medium};
  }
`

export const Temperature = styled.p`
  margin-top: ${({ theme }) => theme.margin.medium};
  font-size: ${({ theme }) => theme.fonts.fontSizes.xLarge};

  @media (max-width: ${({ theme }) => theme.media.largeDesktop}) {
    font-size: ${({ theme }) => theme.fonts.fontSizes.large};
  }
`

export const Description = styled.p`
  margin-top: ${({ theme }) => theme.margin.small};

  font-size: ${({ theme }) => theme.fonts.fontSizes.large};
  text-transform: capitalize;

  @media (max-width: ${({ theme }) => theme.media.largeDesktop}) {
    font-size: ${({ theme }) => theme.fonts.fontSizes.medium};
  }
`
