import { FC } from 'react'

import { TodayWeatherCard } from '@components/TodayWeatherCard'
import { MemoizedWeatherCard as WeatherCard } from '@components/WeatherCard'
import { useGeolocation } from '@hooks/useGeolocation'
import { useWeather } from '@hooks/useWeather'
import { formatDate, formatTime } from '@utils/formatDateTime'

import { defaultData, staticData } from './config'
import { Container, LoadingIndicator, SliderContainer } from './styled'
import { WeatherDisplayProps } from './types'
import { ErrorBoundary } from '../ErrorBoundary'
import { useBackground } from '../Provider/BackgroundProvider'

const WeatherDisplay: FC<WeatherDisplayProps> = (props) => {
  const { isHourly, selectedCity } = props
  const {
    location: { latitude, longitude },
  } = useGeolocation()
  const { currentWeather, forecastWeather, hourlyWeather, loading, error } =
    useWeather({ selectedCity, latitude, longitude })

  const { weatherDescription, setWeatherDescription } = useBackground()

  if (currentWeather?.description !== weatherDescription) {
    setWeatherDescription(currentWeather?.description || 'clear sky')
  }

  let errorMessage = ''
  if (error) {
    if (error.toString().includes('404')) {
      errorMessage = "Sorry, we don't have information about your city."
    } else {
      errorMessage = error.toString()
    }
  }

  if (loading) {
    return (
      <Container>
        <LoadingIndicator>{staticData.loading}</LoadingIndicator>
      </Container>
    )
  }

  return (
    <ErrorBoundary errorMessage={errorMessage}>
      <Container>
        <TodayWeatherCard
          city={currentWeather?.cityName || defaultData.cityName}
          temperature={currentWeather?.temperature || defaultData.temperature}
          description={currentWeather?.description}
          icon={currentWeather?.icon}
        />
        <SliderContainer>
          {isHourly
            ? hourlyWeather?.map(
                ({ time, temperature, description, timestamp, icon, date }) => (
                  <WeatherCard
                    key={timestamp}
                    day={formatTime(time)}
                    icon={icon}
                    temperature={temperature}
                    description={description}
                    date={date}
                  />
                )
              ) || <>{staticData.noData}</>
            : forecastWeather?.map(
                ({ date, temperature, description, icon }) => (
                  <WeatherCard
                    key={date}
                    day={formatDate(date)}
                    icon={icon}
                    temperature={temperature}
                    description={description}
                    date={date}
                  />
                )
              ) || <>{staticData.noData}</>}
        </SliderContainer>
      </Container>
    </ErrorBoundary>
  )
}

export { WeatherDisplay }
