import { HOURS_IN_HALF_DAY } from '@constants/config'

export const formatDate = (dateString: string) => {
  const date = new Date(dateString)
  const day = String(date.getDate()).padStart(2, '0')
  const month = String(date.getMonth() + 1).padStart(2, '0')
  return `${day}.${month}`
}

export const formatTime = (time: string) => {
  const date = new Date()
  const [hours, minutes] = time.split(':')
  date.setHours(parseInt(hours, 10), parseInt(minutes, 10))

  return date.toLocaleTimeString('en-US', {
    hour: 'numeric',
    minute: '2-digit',
    hour12: true,
  })
}

export const getDayFromDate = (dateString: string) => {
  const date = new Date(dateString)
  const options: Intl.DateTimeFormatOptions = { weekday: 'long' }
  return date.toLocaleDateString('en-US', options)
}

export const formatCalendarDate = (dateTime: string) => {
  const date = new Date(dateTime)
  const hours = date.getHours()
  const minutes = String(date.getMinutes()).padStart(2, '0')

  const ampm = hours >= HOURS_IN_HALF_DAY ? 'PM' : 'AM'
  const formattedHours = String(hours % HOURS_IN_HALF_DAY || HOURS_IN_HALF_DAY)

  const formattedTime = `${formattedHours}:${minutes} ${ampm}`

  return formattedTime
}
