import { FC, useCallback, useState } from 'react'

import { MemoizedCitySearch as CitySearch } from '@components/CitySearch'
import { CurrentDateTime } from '@components/CurrentDateTime'
import { UserCalendar } from '@components/UserCalendar'
import { WeatherDisplay } from '@components/WeatherDisplay'
import { WeatherToggleButtons } from '@components/WeatherToggleButtons'

import {
  MainContainer,
  WeatherGrid,
  SearchBox,
  CalendarBox,
  WeatherHeader,
  WeatherSlider,
  WeatherSwitchBox,
} from './styled'

const Weather: FC = () => {
  const [isHourly, setIsHourly] = useState<boolean>(false)
  const [selectedCity, setSelectedCity] = useState<string>('')

  const handleSetSelectedCity = useCallback((city: string) => {
    setSelectedCity(city)
  }, [])

  const toggleWeatherMode = (hourly: boolean) => () => {
    setIsHourly(hourly)
  }

  return (
    <MainContainer>
      <WeatherGrid>
        <WeatherHeader>
          <CurrentDateTime />
        </WeatherHeader>
        <SearchBox>
          <CitySearch setSelectedCity={handleSetSelectedCity} />
        </SearchBox>
        <CalendarBox>
          <UserCalendar />
        </CalendarBox>
        <WeatherSwitchBox>
          <WeatherToggleButtons setIsHourly={toggleWeatherMode} />
        </WeatherSwitchBox>
        <WeatherSlider>
          <WeatherDisplay isHourly={isHourly} selectedCity={selectedCity} />
        </WeatherSlider>
      </WeatherGrid>
    </MainContainer>
  )
}

export { Weather }
