import { useEffect, useState } from 'react'

import { searchCities } from '@services/cityService'
import { City } from 'src/types/city'

import { useDebounce } from './useDebounce'

export const useCitySearch = (inputValue: string) => {
  const [cities, setCities] = useState<City[]>([])
  const [error, setError] = useState<string | null>(null)
  const [loading, setLoading] = useState<boolean>(false)
  const debounceInputValue = useDebounce(inputValue, 500)

  useEffect(() => {
    const fetchCities = async () => {
      setLoading(true)
      if (debounceInputValue) {
        try {
          const results = await searchCities(debounceInputValue)
          setCities(results)
          setError(null)
        } catch (error) {
          if (error instanceof Error) setError(error.message)
        } finally {
          setLoading(false)
        }
      } else {
        setCities([])
        setLoading(false)
      }
    }

    fetchCities()
  }, [debounceInputValue])

  return { cities, loading, error }
}
