import { gapi } from 'gapi-script'
import { Dispatch, SetStateAction } from 'react'

import { CalendarEvent } from '@/types/calendar'
import { API_CONFIG, GOOGLE_CALENDAR_EVENTS_CONFIG } from '@constants/config'

export const initGapiClient = async () => {
  await gapi.client.init({
    apiKey: API_CONFIG.GOOGLE_API_KEY,
    clientId: API_CONFIG.GOOGLE_CALENDAR_CLIENT_ID,
    scope: API_CONFIG.GOOGLE_CALENDAR_SCOPE,
  })

  await gapi.client.load('calendar', 'v3')
}

export const getAuthInstance = () => gapi.auth2.getAuthInstance()

export const loadEvents = async (
  setEvents: Dispatch<SetStateAction<CalendarEvent[]>>,
  setError: Dispatch<SetStateAction<string>>,
  setLoading: Dispatch<SetStateAction<boolean>>
) => {
  setLoading(true)
  try {
    const response = await gapi.client.calendar.events.list(
      GOOGLE_CALENDAR_EVENTS_CONFIG
    )

    const fetchedEvents = response.result.items || []
    setEvents(fetchedEvents)
  } catch (error) {
    if (error instanceof Error) setError(error.message)
  } finally {
    setLoading(false)
  }
}
