import styled from 'styled-components'

export const Card = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background: none;

  text-align: center;

  margin: 0 ${({ theme }) => theme.margin.large};

  min-height: ${({ theme }) => theme.height.small};

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    min-height: auto;
    margin: ${({ theme }) => theme.margin.large};
  }
  @media (max-width: ${({ theme }) => theme.media.phone}) {
    margin: ${({ theme }) => theme.margin.medium};
  }
`

export const Day = styled.h3`
  font-size: ${({ theme }) => theme.fonts.fontSizes.medium};

  @media (max-width: ${({ theme }) => theme.media.phone}) {
    font-size: ${({ theme }) => theme.fonts.fontSizes.small};
  }
`

export const WeatherIcon = styled.img`
  margin-top: ${({ theme }) => theme.margin.medium};

  width: ${({ theme }) => theme.iconSize.medium};
  height: ${({ theme }) => theme.iconSize.medium};

  @media (max-width: ${({ theme }) => theme.media.smallDesktop}) {
    width: ${({ theme }) => theme.iconSize.small};
    height: ${({ theme }) => theme.iconSize.small};
  }
  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    width: ${({ theme }) => theme.iconSize.extraSmall};
    height: ${({ theme }) => theme.iconSize.extraSmall};
  }
`

export const Temperature = styled.p`
  justify-self: flex-end;
  margin-top: ${({ theme }) => theme.margin.large};
  margin-bottom: ${({ theme }) => theme.margin.small};

  font-size: ${({ theme }) => theme.fonts.fontSizes.large};
  font-weight: ${({ theme }) => theme.fonts.fontWeights.bold};

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    font-size: ${({ theme }) => theme.fonts.fontSizes.medium};
  }
`
