import {
  fetchCurrentWeatherByCity,
  fetchCurrentWeatherByCoordinates,
  fetchHourlyWeatherByCity,
  fetchHourlyWeatherByCoordinates,
  fetchWeatherForecastByCity,
  fetchWeatherForecastByCoordinates,
} from '@/services/weatherService'
import {
  WeatherData,
  WeatherForecastData,
  WeatherHourlyData,
} from '@/types/weather'

import {
  fetchWeatherError,
  fetchWeatherStart,
  setCurrentWeather,
  setForecastWeather,
  setHourlyWeather,
} from '../actions/weatherActions'
import { AppDispatch } from '../types'

const fetchCurrentWeather = async (
  city?: string,
  latitude?: number,
  longitude?: number
): Promise<WeatherData | null> => {
  if (city) {
    return await fetchCurrentWeatherByCity(city)
  } else if (latitude && longitude) {
    return await fetchCurrentWeatherByCoordinates(latitude, longitude)
  }
  throw new Error('Coordinates or City are required')
}

const fetchHourlyWeather = async (
  city?: string,
  latitude?: number,
  longitude?: number
): Promise<WeatherHourlyData[] | null> => {
  if (city) {
    return await fetchHourlyWeatherByCity(city)
  } else if (latitude && longitude) {
    return await fetchHourlyWeatherByCoordinates(latitude, longitude)
  }
  throw new Error('Coordinates or City are required')
}

const fetchDailyForecast = async (
  city?: string,
  latitude?: number,
  longitude?: number
): Promise<WeatherForecastData | null> => {
  if (city) {
    return await fetchWeatherForecastByCity(city)
  } else if (latitude && longitude) {
    return await fetchWeatherForecastByCoordinates(latitude, longitude)
  }
  throw new Error('Coordinates or City are required')
}

export const fetchWeatherData = (
  city?: string,
  latitude?: number | null,
  longitude?: number | null
) => {
  return async (dispatch: AppDispatch) => {
    try {
      dispatch(fetchWeatherStart())

      const currentWeather = await fetchCurrentWeather(
        city,
        latitude!,
        longitude!
      )
      dispatch(setCurrentWeather(currentWeather))

      const hourlyWeather = await fetchHourlyWeather(
        city,
        latitude!,
        longitude!
      )
      dispatch(setHourlyWeather(hourlyWeather))

      const dailyForecast = await fetchDailyForecast(
        city,
        latitude!,
        longitude!
      )
      dispatch(setForecastWeather(dailyForecast))
    } catch (error) {
      dispatch(
        fetchWeatherError(
          error instanceof Error ? error.message : 'Unknown error'
        )
      )
    }
  }
}
