import styled from 'styled-components'

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
`

export const InputContainer = styled.div`
  margin-bottom: ${({ theme }) => theme.margin.medium};
  width: 100%;

  display: flex;
  align-items: center;

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    justify-content: center;
  }
`
export const ButtonContainer = styled.div`
  margin-left: ${({ theme }) => theme.margin.medium};

  @media (max-width: ${({ theme }) => theme.media.phone}) {
    margin-left: ${({ theme }) => theme.margin.small};
  }
`

export const CityList = styled.ul`
  overflow-y: auto;
  border-radius: 5px;

  background-color: ${({ theme }) => theme.colors.white};
  list-style-type: none;

  align-self: center;

  width: 100%;
  max-height: 150px;

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    max-height: 100px;
    max-width: 650px;

    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
  }
`
export const CityItem = styled.li`
  padding: ${({ theme }) => theme.paddings.medium};

  display: flex;
  justify-content: space-between;
  align-items: center;

  color: ${({ theme }) => theme.colors.black};
  cursor: pointer;

  &:hover {
    background-color: ${({ theme }) => theme.colors.darkWhite};
    color: ${({ theme }) => theme.mediumBlack};
  }

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    padding: ${({ theme }) => theme.paddings.large};
  }
  @media (max-width: ${({ theme }) => theme.media.phone}) {
    padding: ${({ theme }) => theme.paddings.medium};
  }
`

export const Text = styled.span`
  font-weight: ${({ theme }) => theme.fonts.fontWeights.medium};
  font-size: ${({ theme }) => theme.fonts.fontSizes.medium};

  @media (max-width: ${({ theme }) => theme.media.phone}) {
    font-size: ${({ theme }) => theme.fonts.fontSizes.small};
  }
`

export const ErrorContainer = styled.div`
  padding: ${({ theme }) => theme.paddings.medium};
`

export const Message = styled.p`
  display: block;
  min-height: 20px;
  color: ${({ theme }) => theme.colors.darkGray};
  font-size: ${({ theme }) => theme.fonts.fontSizes.medium};
  text-align: start;
  margin-bottom: ${({ theme }) => theme.margin.small};

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    font-size: ${({ theme }) => theme.fonts.fontSizes.small};
    margin-left: ${({ theme }) => theme.margin.small};
  }
`
