import { FC, memo } from 'react'

import { Button } from '@components/UI/Button'
import { Input } from '@components/UI/Input'
import { useCityDropdown } from '@hooks/useCityDropdown'
import { useCitySearch } from '@hooks/useCitySearch'

import { defaultData } from './config'
import {
  ButtonContainer,
  CityItem,
  CityList,
  Container,
  ErrorContainer,
  Message,
  InputContainer,
  Text,
} from './styled'
import { CitySearchProps } from './types'

const CitySearch: FC<CitySearchProps> = ({ setSelectedCity }) => {
  const {
    inputValue,
    setInputValue,
    isOpen,
    setIsOpen,
    handleInputChange,
    handleFocus,
    containerRef,
    error,
  } = useCityDropdown('')

  const { cities: foundCities } = useCitySearch(
    inputValue.length >= 3 ? inputValue : ''
  )

  const handleCitySelect = (city: string) => () => {
    setInputValue(city)
    setSelectedCity(city)
    setIsOpen(false)
  }

  const handleSubmitClick = () => {
    if (inputValue) {
      setSelectedCity(inputValue)
      setIsOpen(false)
    }
  }

  return (
    <Container>
      <Message>{error ? error : defaultData.inputSubscription}</Message>
      <InputContainer ref={containerRef}>
        <Input
          type="text"
          value={inputValue}
          onChange={handleInputChange}
          placeholder="Enter City..."
          onFocus={handleFocus}
        />
        <ButtonContainer>
          <Button onClick={handleSubmitClick}>Search</Button>
        </ButtonContainer>
      </InputContainer>
      {isOpen && (
        <CityList>
          {foundCities.length > 0
            ? foundCities.map(({ id, name, country }) => (
                <CityItem key={id} onClick={handleCitySelect(name)}>
                  <Text>{name}</Text>
                  <Text>{country}</Text>
                </CityItem>
              ))
            : inputValue && (
                <ErrorContainer>
                  <Text>{defaultData.noCities}</Text>
                </ErrorContainer>
              )}
        </CityList>
      )}
    </Container>
  )
}

export const MemoizedCitySearch = memo(CitySearch)
