import { createGlobalStyle } from 'styled-components'

import { GlobalStylesProps } from '@/types/styles'
import { useBackground } from '@components/Provider/BackgroundProvider'
import { weatherBackground } from '@constants/background'

const GlobalStyles = createGlobalStyle<GlobalStylesProps>`
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    body{
        min-height: 100vh;
        background-image: ${(props) => `url(${props.background})`};
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        
    }
    a{
        text-decoration: none;
        color: inherit
    }
    button{
        cursor: pointer;
    }
`

export const StyledGlobal = () => {
  const { weatherDescription } = useBackground()
  const background =
    weatherBackground[weatherDescription] || weatherBackground['clear sky']
  return <GlobalStyles background={background} />
}
