import axios from 'axios'

import { API_CONFIG } from '@constants/config'
import { City, ElasticsearchResponse } from 'src/types/city'

export const searchCities = async (value: string): Promise<City[]> => {
  const response = await axios.post<ElasticsearchResponse>(
    `${API_CONFIG.ELASTICSEARCH_ENDPOINT}/cities/_search`,
    {
      query: {
        bool: {
          must: [
            {
              wildcard: {
                name: {
                  value: `*${value}*`,
                  boost: 2,
                },
              },
            },
          ],
        },
      },
      from: 0,
      size: 10000,
    },
    {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `ApiKey ${API_CONFIG.ELASTICSEARCH_API_KEY}`,
      },
    }
  )

  const results: City[] = response.data.hits.hits
    .map((hit) => hit._source)
    .filter((source) => source.id && source.name && source.country)
    .slice(0, 10)

  return results
}
