import { FC } from 'react'

import { roundTemperature } from '@/utils/formatTemperature'
import { getWeatherIconURL } from '@utils/getWeatherIcon'
import { TodayWeatherCardProps } from 'src/types/componentProps'

import { defaultData } from './config'
import {
  Container,
  Day,
  Description,
  Temperature,
  TextContainer,
  WeatherIcon,
} from './styled'

const TodayWeatherCard: FC<TodayWeatherCardProps> = (props) => {
  const {
    city = defaultData.cardTitle,
    temperature = defaultData.temperature,
    description = defaultData.description,
    icon,
  } = props

  const weatherIconURL = icon ? getWeatherIconURL(icon) : defaultData.image

  return (
    <Container>
      <WeatherIcon src={weatherIconURL} alt={`Weather icon for ${city}`} />
      <TextContainer>
        <Day>
          {defaultData.cardTitle} {city}
        </Day>
        <Temperature>{roundTemperature(temperature)}</Temperature>
        <Description>{description}</Description>
      </TextContainer>
    </Container>
  )
}

export { TodayWeatherCard }
