import styled from 'styled-components'

export const MainContainer = styled.main`
  background-color: ${({ theme }) => theme.colors.whiteBlur};
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 1px solid ${({ theme }) => theme.colors.grayBlur};

  padding: 5%;
  margin: 5%;

  @media (max-width: ${({ theme }) => theme.media.phone}) {
    padding: 1%;
  }
`

export const WeatherGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 100px 100px 100px 1fr;
  grid-column-gap: 5%;
  grid-row-gap: 10px;

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
`

export const WeatherHeader = styled.section`
  grid-area: 1 / 1 / 2 / 2;
  padding: ${({ theme }) => theme.paddings.small};
`

export const SearchBox = styled.section`
  grid-area: 1 / 2 / 3 / 3;

  padding: ${({ theme }) => theme.paddings.small};
  height: ${({ theme }) => theme.height.medium};

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    height: auto;
    grid-area: auto;
  }
`

export const CalendarBox = styled.section`
  grid-area: 2 / 1 / 4 / 2;

  padding: ${({ theme }) => theme.paddings.small};

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    height: auto;
    grid-area: auto;
  }
`

export const WeatherSwitchBox = styled.section`
  grid-area: 3 / 2 / 4 / 3;

  padding: ${({ theme }) => theme.paddings.small};

  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    grid-area: auto;
  }
`

export const WeatherSlider = styled.section`
  grid-area: 4 / 1 / 5 / 3;

  padding: ${({ theme }) => theme.paddings.small};

  min-height: 250px;

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    grid-area: auto;
  }
`
