import { createSlice, PayloadAction } from '@reduxjs/toolkit'

import {
  WeatherData,
  WeatherForecastData,
  WeatherHourlyData,
} from '@/types/weather'

import { WeatherState } from '../types'

const initialState: WeatherState = {
  currentWeather: null,
  forecastWeather: null,
  hourlyWeather: null,
  loading: false,
  error: '',
}

const weatherSlice = createSlice({
  name: 'weather',
  initialState,
  reducers: {
    setCurrentWeather(state, action: PayloadAction<WeatherData>) {
      state.currentWeather = action.payload
      state.loading = false
      state.error = ''
    },
    setForecastWeather(state, action: PayloadAction<WeatherForecastData>) {
      state.forecastWeather = action.payload
      state.loading = false
      state.error = ''
    },
    setHourlyWeather(state, action: PayloadAction<WeatherHourlyData[]>) {
      state.hourlyWeather = action.payload
      state.loading = false
      state.error = ''
    },
    fetchWeatherStart(state) {
      state.loading = true
      state.error = ''
    },
    fetchWeatherError(state, action: PayloadAction<string>) {
      state.loading = false
      state.error = action.payload
    },
  },
})

export const {
  setCurrentWeather,
  setForecastWeather,
  setHourlyWeather,
  fetchWeatherStart,
  fetchWeatherError,
} = weatherSlice.actions

export default weatherSlice.reducer
