import { formatCalendarDate } from '@utils/formatDateTime'

import { EventCard, Date, Event } from './styled'
import { EventListProps } from './types'
import { Subscription } from '../styled'
import { defaultData } from './config'

export const EventList: React.FC<EventListProps> = (props) => {
  const { events, loading } = props
  if (loading) {
    return <Subscription>{defaultData.loading}</Subscription>
  }

  if (events.length === 0) {
    return <Subscription>{defaultData.noEvents}</Subscription>
  }

  return (
    <>
      {events.map(({ id, summary, start }) => (
        <EventCard key={id}>
          <Date>{start.dateTime && formatCalendarDate(start.dateTime)}</Date>
          <Event>{summary}</Event>
        </EventCard>
      ))}
    </>
  )
}
