import { FC, memo } from 'react'

import { getDayFromDate } from '@/utils/formatDateTime'
import { roundTemperature } from '@/utils/formatTemperature'
import { getWeatherIconURL } from '@/utils/getWeatherIcon'
import { WeatherCardProps } from 'src/types/componentProps'

import { defaultData } from './config'
import { Card, Day, Temperature, WeatherIcon } from './styled'

const WeatherCard: FC<WeatherCardProps> = (props) => {
  const {
    day = defaultData.day,
    icon,
    temperature = defaultData.temperature,
    date = null,
  } = props

  const weatherIconURL = icon ? getWeatherIconURL(icon) : defaultData.image
  return (
    <Card>
      <Day>{day}</Day>
      {date && (
        <>
          <Day>{getDayFromDate(date)}</Day>
        </>
      )}
      <WeatherIcon src={weatherIconURL} alt={`Weather icon for ${day}`} />
      <Temperature>{roundTemperature(temperature)}</Temperature>
    </Card>
  )
}

export const MemoizedWeatherCard = memo(WeatherCard)
