import { createContext, FC, ReactNode, useContext, useState } from 'react'

interface BackgroundContextType {
  weatherDescription: string
  setWeatherDescription: (description: string) => void
}

const BackgroundContext = createContext<BackgroundContextType | undefined>(
  undefined
)

export const BackgroundProvider: FC<{ children: ReactNode }> = ({
  children,
}) => {
  const [weatherDescription, setWeatherDescription] =
    useState<string>('clear sky')

  return (
    <BackgroundContext.Provider
      value={{ weatherDescription, setWeatherDescription }}
    >
      {children}
    </BackgroundContext.Provider>
  )
}

export const useBackground = () => {
  const context = useContext(BackgroundContext)
  if (context === undefined) {
    throw new Error('useBackground must be used within a provider')
  }
  return context
}
