import { createAction } from '@reduxjs/toolkit'

import {
  WeatherData,
  WeatherForecastData,
  WeatherHourlyData,
} from '@/types/weather'

export const fetchWeather = createAction<{
  city?: string
  latitude?: number
  longitude?: number
}>('weather/fetchWeather')
export const fetchWeatherStart = createAction('weather/fetchWeatherStart')
export const fetchWeatherError = createAction<string>(
  'weather/fetchWeatherError'
)
export const setCurrentWeather = createAction<WeatherData | null>(
  'weather/setCurrentWeather'
)
export const setHourlyWeather = createAction<WeatherHourlyData[] | null>(
  'weather/setHourlyWeather'
)
export const setForecastWeather = createAction<WeatherForecastData | null>(
  'weather/setForecastWeather'
)
