import { FC } from 'react'

import { Button } from '@components/UI/Button'

import { WeatherToggleButtonsProps } from './types'

const WeatherToggleButtons: FC<WeatherToggleButtonsProps> = ({
  setIsHourly,
}) => {
  return (
    <>
      <Button onClick={setIsHourly(false)}>Daily</Button>
      <Button onClick={setIsHourly(true)}>Hourly</Button>
    </>
  )
}

export { WeatherToggleButtons }
