import styled from 'styled-components'

export const EventCard = styled.li`
  padding: ${({ theme }) => theme.paddings.large} 0;

  display: flex;
  align-items: center;

  width: 100%;

  color: ${({ theme }) => theme.colors.black};

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    padding: 0;
    padding-bottom: ${({ theme }) => theme.paddings.large};
  }

  @media (max-width: ${({ theme }) => theme.media.phone}) {
    padding: ${({ theme }) => theme.paddings.small} 0;
  }
`

export const Date = styled.p`
  padding: ${({ theme }) => theme.paddings.small};
  color: ${({ theme }) => theme.colors.white};
  background: ${({ theme }) => theme.colors.darkGray};

  border-radius: 10px;
  max-width: 80px;
  width: 100%;

  text-align: center;
  font-size: ${({ theme }) => theme.fonts.fontSizes.small};

  @media (max-width: ${({ theme }) => theme.media.phone}) {
    max-width: 70px;
  }
`

export const Event = styled.p`
  margin-left: ${({ theme }) => theme.margin.medium};
  overflow-x: hidden;
  white-space: normal;
  text-overflow: ellipsis;

  text-align: start;
  font-size: ${({ theme }) => theme.fonts.fontSizes.medium};
  font-weight: ${({ theme }) => theme.fonts.fontWeights.bold};
  max-width: 150px;

  @media (max-width: ${({ theme }) => theme.media.tablet}) {
    max-width: 250px;
  }

  @media (max-width: ${({ theme }) => theme.media.phone}) {
    max-width: 150px;
    font-size: ${({ theme }) => theme.fonts.fontSizes.small};
    margin-left: ${({ theme }) => theme.margin.small};
  }
`
