import { FC, useState } from 'react'

import { Button } from '@components/UI/Button'
import { useCalendar } from '@hooks/useCalendar'

import {
  CalendarContainer,
  Container,
  ButtonContainer,
  Subscription,
} from './styled'
import { SignOutModal } from '../SignOutModal'
import { EventList } from './EventsList'

export const UserCalendar: FC = () => {
  const { events, isSignedIn, signIn, signOut, loading } = useCalendar()
  const [isModalOpen, setModalOpen] = useState<boolean>(false)

  const handleSignOut = () => {
    signOut()
    setModalOpen(false)
  }

  return (
    <Container>
      <SignOutModal
        isOpen={isModalOpen}
        onClose={() => setModalOpen(false)}
        onConfirm={handleSignOut}
      />
      {isSignedIn ? (
        <>
          <ButtonContainer>
            <Button onClick={() => setModalOpen(true)}>Sign Out</Button>
          </ButtonContainer>
          <CalendarContainer>
            <EventList events={events} loading={loading} />
          </CalendarContainer>
        </>
      ) : (
        <ButtonContainer>
          <Subscription>
            Sign in to load your google calendar events
          </Subscription>
          <Button onClick={signIn}>Sign In</Button>
        </ButtonContainer>
      )}
    </Container>
  )
}
