import { useState, useRef, useEffect, ChangeEvent } from 'react'

import {
  INPUT_VALIDATION_REGEX,
  validationMessages,
} from '@constants/validation'

export const useCityDropdown = (initialInputValue: string) => {
  const [inputValue, setInputValue] = useState<string>(initialInputValue)
  const [isOpen, setIsOpen] = useState<boolean>(false)
  const [error, setError] = useState<string>('')

  const containerRef = useRef<HTMLDivElement>(null)

  const handleInputChange = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value
    const isValidInput = INPUT_VALIDATION_REGEX.test(value)

    if (!isValidInput) {
      setError(validationMessages.engLetters)
      setInputValue(value)
      setIsOpen(false)
      return
    }
    if (value.length < 3) {
      setError(validationMessages.charQuantity)
      setInputValue(value)
      setIsOpen(false)
      return
    }

    setError('')
    setInputValue(value)
    setIsOpen(true)
  }

  const handleFocus = () => {
    if (inputValue.length >= 3) {
      setIsOpen(true)
    }
  }

  const handleClickOutside = (event: MouseEvent) => {
    if (
      containerRef.current &&
      !containerRef.current.contains(event.target as Node)
    ) {
      setIsOpen(false)
    }
  }

  useEffect(() => {
    document.addEventListener('click', handleClickOutside)
    return () => {
      document.removeEventListener('click', handleClickOutside)
    }
  }, [])

  return {
    inputValue,
    setInputValue,
    isOpen,
    setIsOpen,
    handleInputChange,
    handleFocus,
    containerRef,
    error,
  }
}
